<template>
  <v-app id="inspire">
    <v-navigation-drawer v-model="drawerLeft" app color="blue-grey darken-3" dark left>
      <theNavigation />

      <template v-slot:append>
        <v-divider></v-divider>
        <div class>
          <v-list bottom fixed>
            <v-list-item>
              <v-switch v-model="goDark" :label="`Dark Mode ${goDark ? 'On' : 'Off'}`"></v-switch>
            </v-list-item>
          </v-list>
        </div>
      </template>
    </v-navigation-drawer>
    <!-- <v-navigation-drawer
			v-model="drawerRight"
			app
			clipped
			right
			class="overflow-visible"
		>
			<v-card shaped elevation="0" class="pa-2" color="transparent">
				<v-subheader>REPORTS</v-subheader>
				<activeCard
					v-for="(item, index) in activeEEs"
					:key="index"
					:cardData="item"
				/>
			</v-card>
    </v-navigation-drawer>-->

    <v-app-bar app color="blue-grey darken-3" dark elevate-on-scroll clipped-right>
      <v-app-bar-nav-icon @click.stop="drawerLeft = !drawerLeft"></v-app-bar-nav-icon>
      <v-tooltip right transition="slide-x-transition" open-delay="250">
        <template v-slot:activator="{ on, attrs }">
          <v-toolbar-title v-bind="attrs" v-on="on">
            A
            <sup>2</sup>
          </v-toolbar-title>
        </template>
        <span>Absence Administration</span>
      </v-tooltip>
      <v-spacer></v-spacer>
    </v-app-bar>

    <v-main>
      <v-container fluid>
        <!-- <v-btn
          @click="drawerRight = !drawerRight"
          depressed
          color="purple"
          dark
          absolute
          right
          top
          style="transform:rotate(-90deg);right:-25px;z-index:1"
          class="mt-3"
        >Active</v-btn>-->
        <v-row>
          <v-col>
            <v-btn depressed color="primary" @click="addNewDialog = true">
              <v-icon left>mdi-plus</v-icon>New
            </v-btn>
          </v-col>
          <v-col>
            <v-select v-model="selectedFilters" :items="filters" label="Filters" multiple>
              <template v-slot:prepend-item>
                <v-list-item ripple @click="toggle">
                  <v-list-item-action>
                    <v-icon :color="selectedFilters.length > 0 ? 'indigo darken-4' : ''">{{ icon }}</v-icon>
                  </v-list-item-action>
                  <v-list-item-content>
                    <v-list-item-title>Select All</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-divider class="mt-2"></v-divider>
              </template>
            </v-select>
          </v-col>
        </v-row>
        <v-row>
          <v-col>
            <v-scroll-x-transition mode="out-in">
              <router-view
                :allEEs="testEEs"
                :loading="loadingData"
                :activeEEs="activeEEs"
                :pendingEEs="pendingEEs"
                :selectedFilters="selectedFilters"
              ></router-view>
            </v-scroll-x-transition>
          </v-col>
        </v-row>
      </v-container>
    </v-main>

    <template>
      <v-row justify="center">
        <addNewEmployee
          :showDialog="addNewDialog"
          :testPlans="testPlans"
          @addNewDialog="addNewDialog = false"
        />
      </v-row>
    </template>
  </v-app>
</template>

<script>
import theNavigation from "@/components/theNavigation.vue";
// import activeCard from '@/components/activeCard.vue';
import addNewEmployee from "@/components/addNewEmployee.vue";
import axios from "axios";
import { db } from "@/db";
export default {
  props: {
    source: String
  },
  components: {
    theNavigation,
    addNewEmployee
  },

  computed: {
    icon() {
      if (this.selectAll) return "mdi-close-box";
      if (this.selectSome) return "mdi-minus-box";
      return "mdi-checkbox-blank-outline";
    },
    selectAll() {
      return this.selectedFilters.length === this.filters.length;
    },
    selectSome() {
      return this.selectedFilters.length > 0 && !this.selectAll;
    }
  },

  data: () => ({
    filters: ["active", "pending"],
    selectedFilters: ["active", "pending"],
    drawerLeft: false,
    drawerRight: null,
    goDark: false,
    activeEEs: [],
    pendingEEs: [],
    addNewDialog: false,
    testEEs: [],
    testPlans: [],
    loadingData: true
  }),
  methods: {
    getPlans: function() {
      var self = this;
      db.collection("testPlans").onSnapshot(function(querySnapshot) {
        self.testPlans = [];

        querySnapshot.forEach(function(doc) {
          self.testPlans.push({ doc: doc.id, ...doc.data() });
        });
      });
    },
    toggle() {
      this.$nextTick(() => {
        if (this.selectAll) {
          this.selectedFilters = [];
        } else {
          this.selectedFilters = this.filters.slice();
        }
      });
    },
    getEEs: function() {
      var self = this;
      self.testEEs = [];
      db.collection("testEEs").onSnapshot(function(querySnapshot) {
        self.testEEs = [];
        self.loadingData = true;
        querySnapshot.forEach(function(doc) {
          self.testEEs.push({ doc: doc.id, ...doc.data() });
          self.loadingData = false;
        });
      });
    },
    getPendingEEs: function() {
      var self = this;
      axios
        .get("/data/pendingEEs.json")
        .then(function(response) {
          self.pendingEEs = response.data;
        })
        .catch(function() {
          alert("fail");
        });
    },
    getActiveEEs: function() {
      var self = this;
      axios
        .get("/data/sampleActive.json")
        .then(function(response) {
          self.activeEEs = response.data;
        })
        .catch(function() {
          alert("fail");
        });
    }
  },
  created: function() {
    this.getEEs();
    this.getPlans();
    if (this.goDark) {
      this.$vuetify.theme.dark = true;
    } else {
      this.$vuetify.theme.dark = false;
    }
  },
  watch: {
    goDark: function() {
      if (this.goDark) {
        this.$vuetify.theme.dark = true;
      } else {
        this.$vuetify.theme.dark = false;
      }
    }
  }
};
</script>
