<template>
  <div>
    <v-dialog v-model="showDialog" persistent max-width="700" top20>
      <v-card>
        <v-toolbar dark color="primary">
          <v-toolbar-title>New Employee</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-btn icon dark @click="closeAddNew">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card class="mx-auto">
          <v-card-title class="title font-weight-regular justify-space-between">
            <span>{{ currentTitle }}</span>
            <v-avatar
              color="primary lighten-2"
              class="subheading white--text"
              size="24"
              v-text="step"
            ></v-avatar>
          </v-card-title>

          <v-window v-model="step">
            <v-window-item :value="1">
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6">
                      <v-select
                        v-model="newitemadd.title"
                        :items="['Mrs.', 'Miss', 'Ms.', 'Mr.', 'Dr.']"
                        label="Title"
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6">
                      <v-select
                        v-model="newitemadd.gender"
                        :items="[
													'Male',
													'Female',
													'Non Binary',
													'Prefer not to Answer',
												]"
                        label="Gender*"
                        required
                      ></v-select>
                    </v-col>
                  </v-row>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field label="First Name*" required v-model="newitemadd.first_name"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field label="M.I." v-model="newitemadd.middle_initial"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field label="Last Name*" required v-model="newitemadd.last_name"></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6">
                      <v-text-field
                        v-model="newitemadd.ssn"
                        @click:append="showSSN = !showSSN"
                        inputmode="decimal"
                        maxlength="9"
                        :append-icon="showSSN ? 'mdi-eye-off' : 'mdi-eye'"
                        label="Social Security Number"
                        :type="showSSN ? 'text' : 'password'"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-menu
                        v-model="menu2"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="newitemadd.dob"
                            label="Date of Birth"
                            append-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker v-model="newitemadd.dob" @input="menu2 = false"></v-date-picker>
                      </v-menu>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-window-item>
            <v-window-item :value="2">
              <v-card-text>
                <v-container>
                  <v-row dense>
                    <v-col cols="12" md="6">
                      <v-text-field
                        v-model="newitemadd.addr1"
                        label="Home Address Line 1*"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field label="Home Address Line 2" v-model="newitemadd.addr2"></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field
                        v-model="newitemadd.city"
                        label="City"
                        hint="example of persistent helper text"
                        persistent-hint
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="2">
                      <v-select
                        :items="stateList"
                        label="State*"
                        required
                        v-model="newitemadd.state"
                      ></v-select>
                    </v-col>
                    <v-col cols="12" md="4">
                      <v-text-field
                        v-model="newitemadd.zip"
                        type="number"
                        label="Zip"
                        required
                        maxlength="5"
                        inputmode="decimal"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        v-model="newitemadd.email"
                        :rules="emailRules"
                        label="Email*"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field
                        v-model="newitemadd.home_phone"
                        label="Home Phone"
                        type="tel"
                        inputmode="decimal"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-text-field
                        v-model="newitemadd.mobile_phone"
                        label="Mobile Phone"
                        type="tel"
                        inputmode="decimal"
                        required
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-window-item>
            <v-window-item :value="3">
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <v-select
                        v-model="newitemadd.bill_event"
                        :items="billEvents"
                        label="Billable Event*"
                        required
                      ></v-select>
                    </v-col>

                    <v-col cols="12" md="6">
                      <v-menu
                        v-model="menu3"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="newitemadd.event_date"
                            label="Date of Event"
                            append-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker v-model="newitemadd.event_date" @input="menu3 = false"></v-date-picker>
                      </v-menu>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-menu
                        v-model="menu4"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="newitemadd.bill_start"
                            label="Billing Start"
                            append-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker v-model="newitemadd.bill_start" @input="menu4 = false"></v-date-picker>
                      </v-menu>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-window-item>
            <v-window-item :value="4">
              <v-card-text>
                <v-container>
                  <v-row v-for="(item, name, index) in availPlans" :key="index">
                    <v-col cols="12">
                      <v-select
                        :items="item"
                        :label="
													name.charAt(0).toUpperCase() +
														name.substr(1) +
														'  Plans'
												"
                      >
                        <template v-slot:item="{ item }">{{ item.plan_name }}</template>
                        <template v-slot:selection="{ item }">{{ item.plan_name }}</template>
                      </v-select>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-window-item>
            <v-window-item :value="5">
              <v-card-text>
                <v-container>
                  <v-row align="center">
                    <v-col cols="auto">
                      <span class="headline">Employee Information</span>
                    </v-col>
                    <v-col>
                      <v-btn text color="primary" @click="step = 1">Edit</v-btn>
                    </v-col>
                  </v-row>
                  <v-row dense>
                    <v-col cols="12" sm="6">
                      <v-select
                        disabled
                        v-model="newitemadd.title"
                        :items="['Mrs.', 'Miss', 'Ms.', 'Mr.', 'Dr.']"
                        label="Title"
                      ></v-select>
                    </v-col>
                    <v-col cols="12" sm="6">
                      <v-select
                        disabled
                        v-model="newitemadd.gender"
                        :items="[
													'Male',
													'Female',
													'Non Binary',
													'Prefer not to Answer',
												]"
                        label="Gender*"
                        required
                      ></v-select>
                    </v-col>
                  </v-row>
                  <v-row dense>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        disabled
                        label="First Name*"
                        required
                        v-model="newitemadd.first_name"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field disabled label="M.I." v-model="newitemadd.middle_initial"></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        disabled
                        label="Last Name*"
                        required
                        v-model="newitemadd.last_name"
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" md="6">
                      <v-text-field
                        disabled
                        v-model="newitemadd.ssn"
                        @click:append="showSSN = !showSSN"
                        inputmode="decimal"
                        maxlength="9"
                        :append-icon="showSSN ? 'mdi-eye-off' : 'mdi-eye'"
                        label="Social Security Number"
                        :type="showSSN ? 'text' : 'password'"
                        required
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-menu
                        disabled
                        v-model="menu2"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            disabled
                            v-model="newitemadd.dob"
                            label="Date of Birth"
                            append-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker disabled v-model="newitemadd.dob" @input="menu2 = false"></v-date-picker>
                      </v-menu>
                    </v-col>
                  </v-row>

                  <v-row dense>
                    <v-col cols="12">
                      <v-row align="center">
                        <v-col cols="auto">
                          <span class="headline">Billing Address</span>
                        </v-col>
                        <v-col>
                          <v-btn text color="primary" @click="step = 2">Edit</v-btn>
                        </v-col>
                      </v-row>
                      <v-row>
                        <v-container>
                          <v-row dense>
                            <v-col dense cols="12" md="6">
                              <v-text-field
                                disabled
                                v-model="newitemadd.addr1"
                                label="Home Address Line 1*"
                                required
                              ></v-text-field>
                            </v-col>
                            <v-col dense cols="12" md="6">
                              <v-text-field
                                disabled
                                label="Home Address Line 2"
                                v-model="newitemadd.addr2"
                              ></v-text-field>
                            </v-col>
                            <v-col dense cols="12" md="6">
                              <v-text-field
                                disabled
                                v-model="newitemadd.city"
                                label="City"
                                hint="example of persistent helper text"
                                persistent-hint
                                required
                              ></v-text-field>
                            </v-col>
                            <v-col dense cols="12" md="2">
                              <v-select
                                disabled
                                :items="stateList"
                                label="State*"
                                required
                                v-model="newitemadd.state"
                              ></v-select>
                            </v-col>
                            <v-col cols="12" md="4">
                              <v-text-field
                                disabled
                                v-model="newitemadd.zip"
                                type="number"
                                label="Zip"
                                required
                                maxlength="5"
                                inputmode="decimal"
                              ></v-text-field>
                            </v-col>
                            <v-col cols="12">
                              <v-text-field
                                disabled
                                v-model="newitemadd.email"
                                :rules="emailRules"
                                label="Email*"
                                required
                              ></v-text-field>
                            </v-col>
                            <v-col cols="12" md="6">
                              <v-text-field
                                disabled
                                v-model="newitemadd.home_phone"
                                label="Home Phone"
                                type="tel"
                                inputmode="decimal"
                                required
                              ></v-text-field>
                            </v-col>
                            <v-col cols="12" md="6">
                              <v-text-field
                                disabled
                                v-model="newitemadd.mobile_phone"
                                label="Mobile Phone"
                                type="tel"
                                inputmode="decimal"
                                required
                              ></v-text-field>
                            </v-col>
                          </v-row>
                        </v-container>
                      </v-row>
                    </v-col>
                  </v-row>
                  <v-row dense>
                    <v-col cols="12">
                      <v-select
                        disabled
                        v-model="newitemadd.bill_event"
                        :items="billEvents"
                        label="Billable Event*"
                        required
                      ></v-select>
                    </v-col>

                    <v-col cols="12" md="6">
                      <v-menu
                        disabled
                        v-model="menu3"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            disabled
                            v-model="newitemadd.event_date"
                            label="Date of Event"
                            append-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          disabled
                          v-model="newitemadd.event_date"
                          @input="menu3 = false"
                        ></v-date-picker>
                      </v-menu>
                    </v-col>
                    <v-col cols="12" md="6">
                      <v-menu
                        disabled
                        v-model="menu4"
                        :close-on-content-click="false"
                        :nudge-right="40"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            disabled
                            v-model="newitemadd.bill_start"
                            label="Billing Start"
                            append-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          disabled
                          v-model="newitemadd.bill_start"
                          @input="menu4 = false"
                        ></v-date-picker>
                      </v-menu>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
            </v-window-item>
          </v-window>
          <v-divider></v-divider>

          <v-card-actions>
            <v-slide-x-transition>
              <template v-if="step > 1">
                <v-btn :disabled="step === 1" text @click="step--">Back</v-btn>
              </template>
            </v-slide-x-transition>
            <v-spacer></v-spacer>
            <template v-if="step < 5">
              <v-btn color="primary" depressed @click="step++">Next</v-btn>
            </template>
            <template v-if="step == 5">
              <v-btn dark color="teal accent-4" depressed @click="pushNew('active')">Submit</v-btn>
            </template>
          </v-card-actions>
        </v-card>
      </v-card>
    </v-dialog>
    <v-dialog v-model="pendingClose" persistent max-width="500">
      <v-card>
        <v-card-title class="headline">Discard?</v-card-title>
        <v-card-text>
          Looks like you&apos;ve started entering data for a new employee.
          Would you like to save this for later?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="deep-purple darken-1"
            text
            @click="
							pendingClose = false;
							pushNew('pending');
						"
          >Save for later</v-btn>
          <v-btn color="green darken-1" text @click="pendingClose = false">Continue Adding</v-btn>

          <v-btn color="red darken-1" text @click="closeAddNew2">Discard</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { db } from "@/db";
import _ from "lodash";
export default {
  name: "addNewEmployee",
  props: ["showDialog", "testPlans"],
  data: () => ({
    menu2: "",
    menu3: "",
    menu4: "",
    menu5: "",
    menu6: "",
    menu7: "",
    showSSN: false,
    step: 1,
    eventDate: new Date().toISOString().substr(0, 10),
    billingDate: new Date().toISOString().substr(0, 10),
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+/.test(v) || "E-mail must be valid"
    ],
    dateOfBirth: new Date().toISOString().substr(0, 10),
    stateList: [
      "Alabama",
      "Alaska",
      "American Samoa",
      "Arizona",
      "Arkansas",
      "California",
      "Colorado",
      "Connecticut",
      "Delaware",
      "District of Columbia",
      "Federated States of Micronesia",
      "Florida",
      "Georgia",
      "Guam",
      "Hawaii",
      "Idaho",
      "Illinois",
      "Indiana",
      "Iowa",
      "Kansas",
      "Kentucky",
      "Louisiana",
      "Maine",
      "Marshall Islands",
      "Maryland",
      "Massachusetts",
      "Michigan",
      "Minnesota",
      "Mississippi",
      "Missouri",
      "Montana",
      "Nebraska",
      "Nevada",
      "New Hampshire",
      "New Jersey",
      "New Mexico",
      "New York",
      "North Carolina",
      "North Dakota",
      "Northern Mariana Islands",
      "Ohio",
      "Oklahoma",
      "Oregon",
      "Palau",
      "Pennsylvania",
      "Puerto Rico",
      "Rhode Island",
      "South Carolina",
      "South Dakota",
      "Tennessee",
      "Texas",
      "Utah",
      "Vermont",
      "Virgin Island",
      "Virginia",
      "Washington",
      "West Virginia",
      "Wisconsin",
      "Wyoming"
    ],
    billEvents: ["Disability", "FMLA", "Absence"],
    newitemadd: {
      title: "",
      gender: "",
      first_name: "",
      middle_initial: "",
      last_name: "",
      ssn: "",
      dob: "",
      addr1: "",
      addr2: "",
      city: "",
      state: "",
      zip: "",
      email: "",
      home_phone: "",
      mobile_phone: "",
      bill_event: "",
      bill_start: "",
      event_date: "",
      bill_sum: "",
      status: "",
      plans: []
    },
    pendingClose: false
  }),
  methods: {
    closeAddNew: function() {
      var self = this;
      if (
        self.newitemadd.title != "" ||
        self.newitemadd.gender != "" ||
        self.newitemadd.first_name != "" ||
        self.newitemadd.middle_initial != "" ||
        self.newitemadd.last_name != "" ||
        self.newitemadd.ssn != "" ||
        self.newitemadd.dob != "" ||
        self.newitemadd.addr1 != "" ||
        self.newitemadd.addr2 != "" ||
        self.newitemadd.city != "" ||
        self.newitemadd.state != "" ||
        self.newitemadd.zip != "" ||
        self.newitemadd.email != "" ||
        self.newitemadd.home_phone != "" ||
        self.newitemadd.mobile_phone != "" ||
        self.newitemadd.bill_event != "" ||
        self.newitemadd.bill_start != "" ||
        self.newitemadd.event_date != "" ||
        self.newitemadd.bill_sum != "" ||
        self.newitemadd.status != ""
      ) {
        self.pendingClose = true;
      } else {
        self.closeAddNew2();
      }
    },
    closeAddNew2: function() {
      var self = this;
      self.$emit("addNewDialog", false);
      self.clearInput();
      self.pendingClose = false;
    },
    clearInput: function() {
      var self = this;
      self.newitemadd.title = "";
      self.newitemadd.gender = "";
      self.newitemadd.first_name = "";
      self.newitemadd.middle_initial = "";
      self.newitemadd.last_name = "";
      self.newitemadd.ssn = "";
      self.newitemadd.dob = "";
      self.newitemadd.addr1 = "";
      self.newitemadd.addr2 = "";
      self.newitemadd.city = "";
      self.newitemadd.state = "";
      self.newitemadd.zip = "";
      self.newitemadd.email = "";
      self.newitemadd.home_phone = "";
      self.newitemadd.mobile_phone = "";
      self.newitemadd.bill_event = "";
      self.newitemadd.bill_start = "";
      self.newitemadd.event_date = "";
      self.newitemadd.bill_sum = "";
      self.newitemadd.status = "";
    },
    pushNew: function(r) {
      var self = this;
      db.collection("testEEs")
        .add({
          title: self.newitemadd.title,
          gender: self.newitemadd.gender,
          first_name: self.newitemadd.first_name,
          middle_initial: self.newitemadd.middle_initial,
          last_name: self.newitemadd.last_name,
          ssn: self.newitemadd.ssn,
          dob: self.newitemadd.dob,
          addr1: self.newitemadd.addr1,
          addr2: self.newitemadd.addr2,
          city: self.newitemadd.city,
          state: self.newitemadd.state,
          zip: self.newitemadd.zip,
          email: self.newitemadd.email,
          home_phone: self.newitemadd.home_phone,
          mobile_phone: self.newitemadd.mobile_phone,
          bill_event: self.newitemadd.bill_event,
          bill_start: self.newitemadd.bill_start,
          event_date: self.newitemadd.event_date,
          bill_sum: self.newitemadd.bill_sum,
          status: r
        })
        .then(function() {
          self.menu = false;
          self.closeAddNew2();
        });
    }
  },
  computed: {
    availPlans: function() {
      var self = this;
      return _.groupBy(self.testPlans, "plan_type");
    },
    fullName: function() {
      var self = this;
      return self.newitemadd.first_name + " " + self.newitemadd.last_name;
    },
    currentTitle() {
      switch (this.step) {
        case 1:
          return "General Information";
        case 2:
          return "Address";
        case 3:
          return "Billable Events";
        case 4:
          return "Billable Benefits";
        case 5:
          return "Review";
        default:
          return "Confirmed";
      }
    }
  }
};
</script>
